import * as React from "react";

import { HornetComponent } from "src/widget/component/hornet-component";
import EventHandler = __React.EventHandler;

/**
 * Composant contenant les élements du dropdown
 */
export class DropdownItem extends HornetComponent<any, any> {
    constructor(props, context?: any) {
        super(props, context);
    }

    /**
     * @inheritDoc
     */
    render() {
        let image;
        let checked = "";
        let onClickEvent = (e) => {
            if (!this.props.disabled) {
                if (this.state.url) {
                    window.location.href = this.state.url;
                }
                else {
                    this.state.action(this.props.valueCurrent);
                }
            }
        };

        /* Si une image existe */
        if (this.props.srcImg) {

            if (typeof this.props.srcImg == "string") {
                image = <img
                    src={this.props.srcImg}
                    className={"imgItem"}
                    alt={this.props.label}/>
            } else {

                image = this.props.srcImg;
            }
        }
        if (this.props.disabled) {
            checked = " checked";
        }
        return (
            <li><a
                tabIndex={0}
                onClick={onClickEvent}
                className={this.props.className + checked}
                ref={ link => this.props.getRef(link) }
                role="listitem"
                onKeyDown={ (e) => this.props.handleKeyDown(e, this.props.action, this.state.url) }
            >{image}
                {this.props.label}
            </a></li>
        )
    }
}


